import $ from 'jquery';
import 'what-input';
import 'imports-loader?jQuery=jquery!owl.carousel';

// Foundation JS relies on a global varaible. In ES6, all imports are hoisted
// to the top of the file so if we used`import` to import Foundation,
// it would execute earlier than we have assigned the global variable.
// This is why we have to use CommonJS require() here since it doesn't
// have the hoisting behavior.
window.jQuery = $;
require('foundation-sites');

// If you want to pick and choose which modules to include, comment out the above and uncomment
// the line below
//import './lib/foundation-explicit-pieces';


$(document).foundation();
$( document ).ready(function() {
  $('#mainBanner').owlCarousel({
    animateOut: 'fadeOut',
    animateIn: 'fadeIn',
    items: 1,
    margin: 0,
    loop: false,
    dots: true,
    nav:false,
    autoplay: true,
    autoplayTimeout:6000,
    mouseDrag: false,
  });
  $("#searchMain").click(function(){
    $("#search-form").show();
    $("input.form-control").focus();
    $('input.form-control').focusout(function(){
      $("#search-form").hide();
    });
  });
  $("#searchMain1").click(function(){
    $("#search-form1").show();
    $("input.form-control").focus();
    $('input.form-control').focusout(function(){
      $("#search-form1").hide();
    });
  });
  $("#fullMenu").click(function(){
    if($("#fullMenu").hasClass('open')){
      $("#fullMenu").removeClass('open');
      $('.full-menu').removeClass('open');
    } else {
      $("#fullMenu").addClass('open');
      $('.full-menu').addClass('open');
    }
  });
  $("#fullMenuMobile").click(function(){
    if($("#fullMenuMobile").hasClass('open')){
      $("#fullMenuMobile").removeClass('open');
      $('.full-menu').removeClass('open');
    } else {
      $("#fullMenuMobile").addClass('open');
      $('.full-menu').addClass('open');
    }
  });
  $(".menu_main li").on('mouseenter', function(e){
    $(this).find('.dropped').addClass('hovered');
    $(".dropped").on('mouseleave',function(){
      $(this).removeClass('hovered');
    });
  });
  $("li.toggle-drop").click(function() {
    var click_id = $(this).data("open");
    $("#" + click_id).toggle();
    $(this).toggleClass("arrow-up");
  })
});

var abc2 = [398,235,346,158,203,544,456,498,126,341,403,180,306,366,113,187,441,466,550,132,256,265,548,509,240,313,225];
var abc = [147,150];
var abc3 = [297,253,414,374,119,487,333];
$.each(abc2, function(index,value){
  $('[data-portal='+ value + ']').addClass('map-current');
})
$('[data-portal]').hover(function(e){
  console.log($(this));
})
$('.map-switch').on('click',function(e){
  $('path.map-current').removeClass('map-current');
  $('.map-switch').removeClass('active');
  let evnt = e.target.id;
  if (evnt == "switch1"){
    $.each(abc, function(index,value){
      $('[data-portal='+ value + ']').addClass('map-current');
    })
    $('#switch1').addClass('active');
    $('.switch-desc').hide();
    $('#desc1').show();
  }
  if (evnt == "switch2") {
    $.each(abc2, function(index,value){
      $('[data-portal='+ value + ']').addClass('map-current');
    })
    $('#switch2').addClass('active');
    $('.switch-desc').hide();
    $('#desc2').show();
  }
  if (evnt == "switch3") {
    $.each(abc3, function(index,value){
      $('[data-portal='+ value + ']').addClass('map-current');
    })
    $('#switch3').addClass('active');
    $('.switch-desc').hide();
    $('#desc3').show();
  }

});
$('.menu_main>li').on('mouseleave',function(){
  $(this).find('.dropped').removeClass('hovered');
})
